.football {
  display: flex;
  flex-direction: column;
  align-items: center;
  &__title {
    font-weight: 400;
    font-size: 16px;
    color: #000;
    margin-bottom: 36px;
  }
  &__field {
    background-color: #5abb58;
    border: 2px solid #000;
    border-radius: 37px;
    width: 720px;
    height: 220px;
    position: relative;
    overflow: hidden;
  }
  &__img {
    width: 50px;
    height: 50px;
    position: absolute;
    transition: transform 0.5s ease;
  }
}
